/* global FB */
import Parse from "../vendors/parse.min.js";

Parse.initialize(process.env.B4A_APP_ID, process.env.B4A_JS_KEY);
Parse.serverURL = process.env.B4A_SERVER_URL;
if (typeof window !== `undefined`) {
  if (window.FB) {
    Parse.FacebookUtils.init({
      // this line replaces FB.init({
      appId: "2479203505722557", // Facebook App ID
      cookie: true, // enable cookies to allow Parse to access the session
      xfbml: true, // initialize Facebook social plugins on the page
      version: "v9.0", // point to the latest Facebook Graph API version, found in FB's App dashboard.
    });
  } else {
    window.fbAsyncInit = () => {
      Parse.FacebookUtils.init({
        // this line replaces FB.init({
        appId: "2479203505722557", // Facebook App ID
        cookie: true, // enable cookies to allow Parse to access the session
        xfbml: true, // initialize Facebook social plugins on the page
        version: "v9.0", // point to the latest Facebook Graph API version, found in FB's App dashboard.
      });
    };
  }
}

