export const crud = (className, modal, mode, suffix, params) => {
  if (mode === "create") {
    return Parse.Cloud.run(`create${className}${suffix}`, {
      ...params,
      modals: [modal],
      models: [modal],
      model: modal,
    });
  }
  if (mode === "read") {
    return Parse.Cloud.run(`find${className}${suffix}`, {
      modals: [modal],
      models: [modal],
      model: modal,
    });
  }
  if (mode === "update") {
    return Parse.Cloud.run(`update${className}${suffix}`, {
      modals: [modal],
      models: [modal],
      model: modal,
    });
  }
  if (mode === "delete") {
    return Parse.Cloud.run(`delete${className}${suffix}`, {
      modals: [modal],
      models: [modal],
      model: modal,
    });
  }
};

