export const getMyCart = async () => {
  return await Parse.Cloud.run(`searchCart`, {
    query: {
      ascending: "createdAt",
      status: {
        f: "equalTo",
        v: "Unpaid",
      },
    },
  });
};

export const getCartForStore = async (storeId) => {
  return await Parse.Cloud.run(`getCartForStore`, {
    storeId,
  });
};

export const getCartForProduct = async (product) => {
  return await Parse.Cloud.run(`searchCart`, {
    query: {
      descending: "createdAt",
      product: {
        f: "equalTo",
        t: "pointer",
        c: "Product",
        v: product.objectId,
      },
    },
  });
};

