export const getProductCartStatus = async (product) => {
  const cart = await Parse.Cloud.run(`searchCart`, {
    query: {
      descending: "createdAt",
      descending: "bid",
      include: "rolePublicProfile",
      product: {
        f: "equalTo",
        t: "pointer",
        c: "Product",
        v: product.id,
      },
    },
  });
  return cart;
};

