/* global FB */
import React, { useEffect, useState } from "react";
import {
  ResponsiveContext,
  Box,
  Button,
  Menu,
  Text,
  Footer,
  Grommet,
} from "grommet";
import "../pages/index.css";
import "./i18n";
import { assign } from "../helpers";
import { Tools, Notification } from "grommet-icons";
import Parse from "../library/parse";
import Loading from "../components/Loading";
import ChatWidget from "../containers/ChatWidget";
import ChatroomList from "../containers/NewChat";
import ChatContext, { defaultValue } from "../context/ChatContext";
import StickyButton from "../components/Button/Sticky";
import UserContext from "../context/UserContext";

const Home = ({ children }) => {

  const _userContext = React.useContext(UserContext)
  const [accountMenu, setAccountMenu] = useState([]);
  const [allowStore, setAllowStore] = useState(false);
  const [roles, setRoles] = useState([]);
  const [user, setUser] = useState(Parse.User && Parse.User.current());
  const [showChat, setShowChat] = useState(false);
  const [chatProps, setChatProps] = useState({});
  const [chatContextValue, setChatContextValue] = useState({
    ...defaultValue,
    maximise: () => {
      setShowChat(true);
    },
    minimise: () => {
      setShowChat(false);
    },
    openChat: (sender, recipient, message) => {
      if (sender && recipient) {
        setShowChat(true);
        setChatProps({
          senderRoleName: sender,
          recipientRoleName:recipient,
          message
        })
      } else {
        setShowChat(true);
      }
    },
  });
  useEffect(() => {
    if (user && user.get("allowStore")) {
      setAllowStore(true);
    }
  }, [user]);

  useEffect(() => {
    const logoutMenu = [
      {
        label: "Dashboard",
        onClick: () => assign("/dashboard"),
      },
      {
        label: "Profile",
        onClick: () => assign("/profile"),
      },
      {
        label: "Sign Out",
        onClick: () => {
          Parse.User.logOut().then(() => {
            setUser(null);
            assign("/");
          });
        },
      },
    ];

    allowStore &&
      logoutMenu.push({
        label: "My Store",
        onClick: () => {
          assign("/account/store");
        },
      });
    allowStore &&
      logoutMenu.push({
        label: "System",
        onClick: () => assign("/system"),
      });

    const accMenu = !user
      ? [
          {
            label: "Sign in",
            onClick: async () => {
              assign("/login");
            },
          },
        ]
      : logoutMenu;
    setAccountMenu(accMenu);
  }, [user]);

  return (
    <Box>
      <ChatContext.Provider value={chatContextValue}>
        <Box
          gridArea="header"
          direction="row"
          align="center"
          pad={{ horizontal: "medium", vertical: "small" }}
          background="brand"
        >
          <Box>
            <Button
              icon={<Tools />}
              label="BidBid"
              onClick={() => {
                assign("/");
              }}
            />
          </Box>
          <Box direction="row" align="center" margin={{ left: "auto" }}>
            <Menu
              size="small"
              label={user ? user.get("email") : "Guest"}
              items={accountMenu}
            />
          </Box>
        </Box>

        <Box align="center" justify="center" pad="small">
          <Box
            pad={{
              bottom: "64px",
            }}
            fill="horizontal"
            width={{
              min: "360px",
            }}
          >
            {children}
            <Loading />
          </Box>
        </Box>
        <Footer margin={{ top: "auto" }} background="brand" pad="medium">
          <Text>Copyright {new Date().getFullYear()} </Text>
        </Footer>
        {
          _userContext && <ChatWidget
            onClick={() => setShowChat(!showChat)}
            expandContainer={showChat}
          >
            {(
              <ChatroomList
                {
                  ...chatProps
                }
                senderRoleName={`User-${_userContext.user.id}`}
              />
            )}
          </ChatWidget>
        }
      </ChatContext.Provider>
    </Box>
  );
};

export default Home;
