import React, { useState, useEffect } from "react";
import { Box, Anchor } from "grommet";
import ContactList from "./ContactList";
import Chat from "../Chatroom";
import { getRolePublicProfile } from '../../library/parse'

const Component = ({ initialActiveId, senderRoleName, recipientRoleName, message }) => {
  
  
  const [activeChat, setActiveChat] = useState({
    senderRoleName,
    recipientRoleName
  });

  useEffect(() => {
    if(recipientRoleName) {
      getRolePublicProfile([recipientRoleName])
      .then(e => {
        setActiveChat({
          senderRoleName,
          recipientRoleName,
          chatroomName: e && e[0] && e[0].rolePublicProfile && e[0].rolePublicProfile.get('name')
        })
      })
    } else {
      setActiveChat({
        senderRoleName,
      })
    }
  }, [senderRoleName, recipientRoleName])
  return (
    <Box height="100%" overflow="scroll">
      {!activeChat.recipientRoleName && (
        <ContactList
          initialActiveId={initialActiveId}
          senderRoleName={senderRoleName}
          onClick={({ recipientRoleName, recipientName }) => {
            setActiveChat({
              senderRoleName,
              recipientRoleName,
              chatroomName: recipientName
            });
          }}
        />
      )}
      {activeChat.recipientRoleName && <Anchor onClick={() => setActiveChat({})}>Back</Anchor>}
      {activeChat.recipientRoleName && <Chat {...activeChat} />}
    </Box>
  );
};

export default Component;
